@import 'mixins';

.main{
    padding: 70px 0;
    position: relative;
    z-index: 6;
    background: $white;
    margin-bottom: 100vh;
    min-height: 100vh;
    & .fixed-logo{
        position: fixed;
        width: 100%;
        height: 100vh;
        z-index: 5;
        top:0;
        pointer-events: none;
        & .big-logo{
            position: relative;
            top: 100px;
            width: 700px;
            float: right;
        }
    }
    & .divider{
        height: 320px;
        width: 1px;
        background: $orange;
        position: relative;
        top:0;
        left:50%;
        margin-bottom: 15px;
        &.short{
            height: 120px;
        }
        &.shorter{
            height: 60px;
        }
    }

    & .section{
        position: relative;
        z-index: 7;
        & .section-title-icon{
            text-align: center;
        }
        & .section-title{
            font-size: 30px;
            color: $orange;
            font-weight: $bold;
            line-height: 40px;
            text-transform: uppercase;
        }
    }
}



/* HOME HEADER */

.home-header{
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: $blue;
    & .big-logo{
        position: relative;
        top: 100px;
        width: 700px;
        float: right;
    }
    & .pattern-gold{
        position: relative;
        top:460px;
        text-align: center;
    }
    & .image-header{
        position: absolute;
        width: 100%;
        max-width: 850px;
        bottom:-200px;
        left: 50px;
    }
    & .container{
        height: 100%;
        & .home-header-text{
            color: $white;
            font-size: 24px;
            position: relative;
            top:460px;
            margin-top: -10px;
        }

    }
}


/* FULL WIDTH BANNER */
.full-banner{
    background: $white;
    & .banner{
        width: 100%;
        padding: 10px 0 40px 0;
        & img{
            width: 100%;
        }
    }
}

/* LAYOUT MOSAICO */

.layout_mosaico{
    // background: $white;
    & a {
        display: inline-block;
        & .testo-mosaico{
            font-size: 16px;
            font-weight: $bold;
            color: $blue;
            text-transform: uppercase;
            margin-top: 5px;
            text-align: left;
            height: 25px;
        } 
    }
    & .section-text{
        color: $blue;
        max-width: 75%;
    }

    & .orizontal-mosaic{
        height: 340px;
        position: relative;
        width:100%;
        & .image-mosaico{
            overflow: hidden;
            height: 310px;
            position: relative;
            & img{
                position: absolute;
                top:50%;
                left: 50%;
                transform: translate(-50%,-50%);
                min-width:100%;
                max-width: none;
                height: 310px;
            }
        }
    }

    & .vertical-mosaic{
        height: 680px;
        position: relative;
        width:100%;
        & .image-mosaico{
            overflow: hidden;
            height: 650px;
            position: relative;
            & img{
                position: absolute;
                top:50%;
                left: 50%;
                transform: translate(-50%,-50%);
                min-width:100%;
                max-width: none;
                height: 650px;
            }
        }
    }
      
}

/* BOX WITH BUTTON */

.box-button{
    padding: 10px 0 40px 0;
    background: $white;
    & .box{
        margin-top: 20px;
        & .orange-button{
            width: 80%;
            margin-top: 25px;
            background: $white;
            &:hover{
                background: $orange;
            }
        }
    }
}

/* FEATURED PRODUCT */

.featured-product{ 
    padding: 10px 0 40px 0;
    background: $white;
    & .product-box{
        margin-top: 20px;
        & .product-name{
            font-size: 16px;
            font-weight: $bold;
            color: $blue;
            margin-top: 10px;
            text-transform: uppercase;
        }
        & .product-price{
            font-weight: $bold;
            color: $orange;
            margin-top: 10px;
        }
        & .product-add-to-cart{
            float: right;
        }
    }
} 

/* DUE COLONNE */

.due_colonne{
    padding: 10px 0 40px 0;
    background: $white;
    & .text{
        padding-left: 25px;
        color: $blue;
        max-width: 75%;
        & h1{
            font-weight: $bold;
            font-size: 30px;
            line-height: 50px;
        }
        & h2{
            font-weight: $regular;
            font-size: 24px;
            padding-bottom: 20px;
        }
        & h3{
            font-weight: $bold;
            font-size: 18px;
            padding-bottom: 0px;
        }
        & a{
            color: $blue;
        }
        & .orange-button{
            margin-top: 20px;
        }
    }
}

/* PRIZE LIST */

.prize-list{
    background: $white;
    padding: 10px 0 40px 0;
    & .prize{
        margin-top: 20px;
    }
}

/* PRIZE DETAILED LIST */

.prize_detailed_list{
    padding: 10px 0 40px 0;
    & .premio-wrap{
            padding: 20px 0;
        & .premio-image{
            max-width: 150px;
            float: right;
        }
        & .premio-title{
            color: $blue;
            font-size: 18px;
            text-transform: uppercase;
            font-weight: bold;
            padding-bottom: 15px;
        }
        & .premio-text{
            color: $blue;
        }
        & hr{
            width: calc(50% + 200px);
            margin-right: 0;
            border-top: 1px solid $blue;
            margin-top: 50px;
        }
    }
}